  position: relative;
  right: 0;
  left: 0;
  width: 100%;
  z-index: 101;
}

.header__section {
  position: relative;
  z-index: 1;
  background-color: #fff;
  box-shadow: 0 1px 10px rgba(130, 130, 134, 0.1);
  padding: 20px 0;
}

@media only screen and (min-width: 768px) {
  .header__section {
    padding: 0;
  }
}

.header__nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

@media only screen and (min-width: 992px) {
  .header__nav {
    flex-wrap: nowrap;
  }
}

.header__brand {
  width: 150px;
  display: inline-block;
  margin-right: 15px;
  line-height: inherit;
  white-space: nowrap;
}

.header__navbar-collapse {
  display: flex !important;
  flex-basis: auto;
  flex-grow: 1;
  align-items: center;
}

.header__navbar-nav {
  display: none;
}

@media only screen and (min-width: 768px) {
  .header__navbar-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;
    margin: 0 0 0 auto;
    list-style: none;
  }
}

.header__navbar-item {
  list-style: none;
}

.header__nav-link {
  display: block;
  padding: 25px 20px;
  color: #5a5f69;
  cursor: pointer;
}

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.header__abs-top {
  position: absolute;
  top: 0;
  bottom: auto;
}

.header__bg-transparent {
  background: transparent;
}

.header__bg-transparent .header__section {
  box-shadow: none;
  background: transparent;
}

.bg-white {
  background-color: #ffffff;
}

.bg-dark {
  background-color: #1e2022;
}

.bg-primary {
  background-color: #00aeff;
}

.bg-secondary {
  background-color: #7f8c8d;
}

.header__custom-bg .header__nav-link {
  color: rgba(255, 255, 255, 0.7);
}

.header__custom-bg .header__nav-link:hover, .header__custom-bg .header__nav-link:focus {
  color: white;
}

.hero {
  position: relative;
}

@media only screen and (min-width: 768px) {
  .hero {
    height: 90vh;
  }
}

.hero.hero-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero.hero-center .container {
  flex: 1;
}

p.lead {
  margin-bottom: 15px;
}

@media only screen and (min-width: 768px) {
  p.lead {
    margin-bottom: 20px;
  }
}

@media only screen and (min-width: 1440px) {
  p.lead {
    margin-bottom: 40px;
  }
}

.hero-content-image-right {
  position: relative;
  padding: 50px 0;
}

@media only screen and (min-width: 768px) {
  .hero-content-image-right .hero-img {
    position: absolute;
    right: 0;
    top: 0;
  }
  .hero-content-image-right .hero-img .hero-img__content {
    width: 600px;
    height: 500px;
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.hero-button-grid .button {
  width: 100%;
  padding: 12px 20px;
}

.hero.hero-gradient {
  background-image: linear-gradient(150deg, #2d1582 0%, #19a0ff 100%);
  background-repeat: repeat-x;
  color: #fff;
  padding-top: 50px;
}

@media only screen and (min-width: 768px) {
  .hero.hero-gradient {
    padding: 0;
  }
}

.hero.hero-gradient h1, .hero.hero-gradient p {
  color: #fff;
}

.hero-svg .svg-section {
  position: absolute;
  top: 0;
  right: 0;
}

.curve-bg-img-hero {
  position: absolute;
  min-height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}

.hero.hero-center--content {
  text-align: center;
}

.hero.hero-center--content.hero-gradient {
  background-image: linear-gradient(150deg, #000000 0%, #1f6190 100%);
}

.hero.hero-center--content .hero-button-grid .button {
  width: auto;
  min-width: 200px;
  font-size: 16px;
  padding: 15px 30px;
  font-weight: 500;
}

.hero.hero-center--content .hero__inner {
  max-width: 768px;
  margin: 0 auto;
}

.footer {
  padding: 35px 10px;
}

.footer h3 {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 10px;
}

.footer p {
  margin: 0;
}

.footer__logo {
  width: 80px;
  margin-bottom: 20px;
  display: inline-block;
}

.copyright {
  padding: 20px 0;
}

.list__link {
  display: block;
  color: #828286;
  font-size: 14px;
  line-height: 22px;
  padding: 5px 0;
  font-style: normal;
}

.footer.footer-gradient-blue {
  background-image: linear-gradient(150deg, #2d1582 0%, #19a0ff 100%);
  background-repeat: repeat-x;
}

.footer-gradient-blue .footer__content {
  padding: 20px 0;
  color: #fff;
}

@media only screen and (min-width: 768px) {
  .footer-gradient-blue .footer__content {
    padding: 80px 20px 10px;
  }
}

.footer-gradient-blue .icon {
  font-size: 20px;
}

.list-inline-item {
  display: inline-block;
  margin-right: 10px;
}

.list-inline-item:last-child {
  margin-right: 0;
}

.row .bottom {
  margin-top: 50px;
}

.footer-curve .footer__content {
  padding: 40px 0;
}

@media only screen and (min-width: 768px) {
  .footer-curve .footer__content {
    padding: 80px 10px;
  }
}

@media only screen and (min-width: 768px) {
  .footer-simple.footer {
    padding: 70px 0;
  }
}

.footer-simple .footer__logo {
  margin-bottom: 10px;
  display: inline-block;
}

@media only screen and (min-width: 768px) {
  .footer-simple .footer__logo {
    margin-bottom: 15px;
  }
}

.footer-simple p {
  margin-top: 10px;
  color: #8c98a4;
  font-size: 12px;
}

.footer-simple .list-inline-item {
  margin-right: 15px;
}

@media only screen and (min-width: 768px) {
  .footer-simple .list-inline-item {
    margin-right: 30px;
  }
}

.footer-simple .list__link {
  font-size: 16px;
}

*, :after, :before {
  box-sizing: border-box;
}

/* Remove padding and margin */
html, body {
  margin: 0;
  padding: 0;
  border: 0;
  height: 100%;
  width: 100%;
}

body {
  color: #1e2022;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-wrap: break-word;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

img {
  vertical-align: middle;
  border-style: none;
}

figure {
  margin: 0;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
}
